<template>
  <div class="details">
    <!-- S  -->
    <div class="headImg">
      <img :src="img" :alt="msg">
      <div class="container">
        <div class="foodTitle">
          {{msg}}
        </div>
        <div class="foodStatus">
          <p :class="foodStatusColor"></p>
          <a>{{foodStatus}}</a>
        </div>
      </div>
    </div>
    <!-- E  -->

    <!-- S  -->
    <div class="nutrients">
      <div class="title">
        100
      </div>
      <div class="content">
        <div class="ul" v-for="(item, i) in nutrients" :key="i">
          <div class="nutrientsName">
            {{item.name}}
          </div>
          <div class="nutrientsData">
            {{item.data}}
          </div>
        </div>
      </div>
    </div>
    <!-- E  -->

    <!-- S  -->
    <div class="evaluate">
      <div class="title">
        
      </div>
      <div class="content">
        {{evaluate}}
      </div>
    </div>
    <!-- E  -->

    <!-- S  -->
    <div class="howEat">
      <div class="title">
        
      </div>
      <div class="content">
        {{howEat}}
      </div>
    </div>
    <!-- E  -->

    <!-- S  -->
    <div class="EdibleTaboo">
      <div class="title">
        
      </div>
      <div class="content">
        <div class="fit">
          <p class="fitBg"></p>
          <a>{{EdibleTaboo[0]}}</a>
        </div>   
        <div class="avoid">
          <p class="avoidBg"></p>
          <a>{{EdibleTaboo[1]}}</a>
        </div>
      </div>
    </div>
    <!-- E  -->

    <!-- S  -->
    <Bottom></Bottom>
    <!-- E  -->

  </div>
</template>

<script>
import Button from 'element-ui'
// 
import Bottom from '@/components/bottom'
// json
import Json from '@/data.json'

export default {
  name: 'Details',
  data () {
    return {
        img: '',  //
        msg: '',  //
        foodStatus: '',   //
        foodStatusColor: '',  //
        nutrients: [],    //
        evaluate: "",   //
        howEat: "",     //
        EdibleTaboo: ""   //
    }
  },
  methods: {
    gainInfo(){   //router-link
      this.msg = this.$route.query.name;
      this.foodStatus = this.$route.query.foodStatus;
      this.img = this.$route.query.img;

      switch (this.foodStatus) {
        case '':
          this.foodStatusColor = 'green'
          this.getJson(Json.greenFoodInfo);
          break;
        case '':
          this.foodStatusColor = 'yellow'
          this.getJson(Json.yellowFoodInfo);
          break;
        case '':
          this.foodStatusColor = 'red'
          this.getJson(Json.redFoodInfo);
          break;
      
        default:
          break;
      }
    },
    getJson(info){  //Json
      this.nutrients = [];  //
      info.map((item, i)=>{  //jsonfoodInfo
        if(this.msg == item.name){  //msg
          this.nutrients = item.nutrients;  // nutrients
          this.evaluate = item.evaluate;    // evaluate
          this.howEat = item.howEat;        // howEat
          this.EdibleTaboo = item.EdibleTaboo;  // EdibleTaboo
        }
      })
    }
  },
  created(){
    
  },
  mounted(){
    this.gainInfo();  //router-link
  },
  components: {
    Bottom
  },
  filters: {
    capitalize: function (value) {
      // const statusMap = {
      //   1: "",
      //   2: ""
      // }
      // return statusMap[value]
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" type="text/css" scoped>
.details{
  width: 90%;
  margin: 0 auto;
  margin-bottom: 60px;
  // 
  .headImg{
    width: 100%;
    margin: 20px auto;
    img{
      width: 100%;
      height: 200px;
      display: block;
    }
    .container{
      width: 100%;
      margin-top: 20px;
      display: flex;
      justify-content: space-between;
      .foodTitle{
        width: 100px;
        line-height: 50px;
        font-size: 24px;
      }
      .foodStatus{
        width: 60px;
        text-align: center;
        p{
          width: 30px;
          height: 30px;
          border-radius: 50%;
          margin: 0 auto;
        }
        .green{
          background: green;
        }
        .yellow{
          background: #e9ec10;
        }
        .red{
          background: red;
        }
        a{
          width: 30px;
          font-size: 12px;

        }
      }
    }
  }

  //
  $greenColor: #2fa548;

  .title{
    font-size: 12px;
    color: $greenColor;
  }

  .nutrients{
    .content{
      display: flex;
      flex-flow: wrap;
      justify-content: space-between;
      .ul{
        width: 33%;
        text-align: center;
        margin: 5px 0;
        .nutrientsName{
          margin: 5px 0;
        }
        .nutrientsData{
          margin: 10px 0;
          color: $greenColor;
        }
      }
    }
  }

  //  //
  .evaluate, .howEat{
    .content{
      font-size: 12px;
      margin: 10px 0;
    }
  }

  //
  .EdibleTaboo{
    .content{
      font-size: 12px;
      margin: 10px 0;
      .fit, .avoid{
        width: 100%;
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
        align-items: center;
        margin: 5px 0;
        p{
          width: 20px;
          height: 20px;
          line-height: 20px;
          text-align: center;
          border-radius: 50%;
        }
        .fitBg{
          background: rgba(47, 165, 72, .8);
        }
        .avoidBg{
          background: rgba(0, 0, 0, .4);
        }
        a{
          display: block;
          width: 310px;
          margin-left: 6px;
        }
      }
    }
  }
}
</style>


