var gloab = [];

var mesArr = [
  {
      "question": "",
      "answer": "  "
  },
  {
      "question": "",
      "answer": ""
  },
  {
      "question": "",
      "answer": ".."
  }
];

var otherMesArr = [
  {
    "question": "",
    "answer": "  "
  },
  {
      "question": "",
      "answer": ""
  },
  {
      "question": "",
      "answer": ".."
  }
];

function unique (arr) {   //
  for(var i=0; i<arr.length; i++){
      for(var j=i+1; j<arr.length; j++){
          if(arr[i].question == arr[j].question){       //splice
              arr.splice(j,1);
              j--;
          }
      }
  }
  return arr;
}  

var allMesArr = unique(otherMesArr.concat(mesArr));




var user = [
  {
    userName: 201601,
    pwd: 123456
  },
  {
    userName: 'admin',
    pwd: 'admin'
  },
  {
    userName: 201602,
    pwd: 123456
  },
  {
    userName: 201622,
    pwd: 123456
  }
]

export default{
  gloab,
  mesArr,
  otherMesArr,
  allMesArr,
  user,
  unique
}


